import { Component } from 'react';
import Neutral from '../Value/Neutral';
import Good from '../Value/Good';
import Bad from '../Value/Bad';
import styles from '../Statistics/Statistics.module.css';

class ComponenStatistics extends Component {
  state = {
    good: 0,
    neutral: 0,
    bad: 0
  };

  onAddition = event => {
    this.setState(state => ({
      [event.target.id]: Number(state[event.target.id] + 1),
    }));
  };

    
  render() {
    return (
      <>
          <h1 className={styles.title}>Please leave feedback</h1>
          <button id="good" type="button" className={styles.btn} onClick={this.onAddition}>Good</button>
          <button id="neutral" className={styles.btn} onClick={this.onAddition}>Neutral</button>
          <button id="bad" className={styles.btn} onClick={this.onAddition}>Bad</button>
        
        
        <div className={styles.counter}>
          <h2 className={styles.title}>Statistics</h2>
          <Good good={this.state.good} />
          <Neutral neutral={this.state.neutral} />
          <Bad bad={this.state.bad} />
        </div>
      </>
    );
  }
}

export default ComponenStatistics;